<template>
  <div
    id="orderquery"
    class="orderquery"
    style="background: rgba(255, 255, 255, 0.9)"
  >
    <div class="topMenu">
      <el-form :inline="true" class="demo-form-inline">
        <el-form-item label="订单号">
          <el-input v-model="orderId" :disabled="true"></el-input>
        </el-form-item>
        <el-form-item label="物流仓">
          <el-input v-model="supplier" :disabled="true"></el-input>
        </el-form-item>
        <el-form-item>
          <a href="https://www.17track.net/zh-cn/" target="_blank"
            >点击前往17</a
          >
        </el-form-item>
        <el-form-item>
          <a :href="alink" target="_blank">点击前往官网</a>
        </el-form-item>
      </el-form>
      <el-button @click="go_route('order_tracing')" type="primary"
        >返回</el-button
      >
    </div>
    <div class="detail">
      <label>订单详情</label>
      <div class="orderdetail" v-show="flag">
        <el-steps space="30" direction="vertical">
          <el-step
            status="process"
            v-for="(item, index) in order_details"
            :key="index"
            :title="item.time"
            :description="item.content"
          ></el-step>
        </el-steps>
      </div>
      <div v-show="!flag">
        <p>{{ msg }}</p>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      orderId: "",
      supplier: "",

      msg: "",
      selectObj: "",
      page: "",
      formData: {
        country: ""
      },
      countries: [],
      warehouses: [],
      tracking_no: "",
      flag: false,
      order_details: [],
      rowData: {}
    };
  },
  computed: {
    alink() {
      let link = "";
      switch (this.supplier) {
        case "DHL":
          link = "https://track.dhlparcel.co.uk/";
          break;
        case "UPS":
          link = "https://www.ups.com/cn/en/Home.page";
          break;
        case "DPD":
          link = "https://tracking.dpd.de/status/gb_GB/404";
          break;
        case "GLS":
          link = "https://gls-group.eu/GROUP/en/parcel-tracking";
          break;
      }
      return link;
    }
  },
  methods: {
    go_route(val) {
      this.$router.push({
        name: val,
        params: {
          selectObj: this.selectObj,
          page: this.page
        }
      });
    },
    search() {
      this.flag = false;
      let url = "server/order_management/get_order_detail/";
      let formData = new FormData();
      formData.append("country", this.formData.country);
      formData.append("tracking_no", this.tracking_no);
      this.$axios.post(this._url2 + url, formData).then(res => {
        console.log(res.data.msg);
        if (res.data.msg === "success") {
          this.flag = true;
          this.order_details = res.data.data;
        } else {
          if (!res.data.data) {
            console.log(res.data.msg);
            this.msg = res.data.msg;
          } else if (res.data.data.length === 0) {
            console.log("未查询到物流信息！");
            this.msg = "未查询到物流信息！";
          }
        }
      });
    }
    // getCountry() {
    //   // todo
    //   let url =
    //     this._url2 + "server/order_management/get_channel_site_country/";
    //   this.$axios.get(url).then(res => {
    //     let countryData = [];
    //     res.data.data1.forEach(i => {
    //       countryData = countryData.concat(i.country);
    //     });
    //     countryData = new Set(countryData);
    //     this.countries = countryData;
    //     this.formData.country = this.countries[0];
    //   });
    // }
  },
  created() {
    this.selectObj = this.$route.params.searchObj;
    this.rowData = this.$route.params.rowData;
    if (this.rowData) {
      this.orderId = this.rowData.AmazonId;
      this.supplier = this.rowData.supplier;
    }
    this.page = this.$route.params.page;
    this.formData.country = this.$route.params.country;
    this.tracking_no = this.$route.params.tracking_no;
    this.search();
    // //获取国家
    // this.getCountry();
  }
};
</script>

<style scoped>
#orderquery {
  background: rgba(255, 255, 255, 0.9) !important;
}
.orderquery {
  padding-top: 30px;
  font-size: 14px;
  width: 60vw;
  margin: 0 auto;
  background: rgba(255, 255, 255, 0.9);
  position: relative;
}

.orderquery .topMenu {
  display: flex;
  justify-content: space-around;
}
.orderquery .topMenu .el-button {
  position: absolute;
  right: 64px;
}
.orderquery .orderdetail {
  margin: 20px auto;
}
a {
  color: blue;
}
.orderquery .tracingsearch {
  display: flex;
}
.orderquery .tracingspan {
  width: 170px;
  line-height: 40px;
}
.orderquery .el-button {
  height: 40px;
  margin-left: 20px;
}
.orderquery .detail {
  padding: 5px 105px;
  min-height: 60px;
}
.orderquery p {
  margin: 20px 0 20px 20px;
}
</style>
<style>
.orderquery .topMenu .el-form {
  display: flex;
  width: 100%;
}
.orderquery .topMenu .el-form-item {
  margin-left: 50px;
}
.orderquery .el-form .el-form-item .el-form-item__label {
  width: 80px;
}
.orderquery .el-form .el-form-item {
  display: flex;
  justify-content: space-between;
}
.orderquery .leftmenu .el-form-item__content:last-child {
  width: 217px;
}
.orderquery .rightcon .el-button {
  margin-left: 20px;
}
</style>



